.ColorBox {
    width: 20%;
    height: 25%;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    margin-bottom: -4px;
    cursor: pointer;
}

.copy-button {
    width: 100px;
    height: 30px;
    position: absolute;
    display: inline-block;
    top: 50%;
    left: 50%;
    margin-left: -50px;
    margin-top: -15px;
    text-align: center;
    outline: none;
    background: rgba(255, 255, 255, .3);
    font-size: 1rem;
    line-height: 30px;
    text-transform: uppercase;
    border: none;
    opacity: 0;
}

.ColorBox:hover .copy-button {
    opacity: 1;
    transition: all.5s;
    cursor: pointer;
}

.copy-button:hover {
    transform: scale(1.2);
}

.box-content {
    position: absolute;
    width: 100%;
    left: 0px;
    bottom: 0px;
    padding: 10px;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 12px;
}

.see-more {
    background: rgba(255, 255, 255, 0.3);
    position: absolute;
    border: none;
    right: 0px;
    bottom: 0px;
    color: white;
    width: 60px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    text-transform: uppercase;
}

.copy-overlay {
    opacity: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    transform: scale(.1);
    transition: transform 0.6s ease-in-out;
}

.copy-overlay.show {
    opacity: 1;
    transform: scale(50);
    z-index: 10;
    position: absolute;
}

.copy-msg {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 4rem;
    transform: scale(0.1);
    opacity: 0;
    color: white;
}

.copy-msg.show {
    transition: all .4s ease-in-out .3s;
    opacity: 1;
    transform: scale(1);
    z-index: 20;
}

.copy-msg h1 {
    font-weight: 400;
    text-shadow: 1px 2px black;
    background: rgba(255, 255, 255, .2);
    width: 100%;
    text-align: center;
    margin-bottom: 0;
    padding: 1rem;
}

.copy-msg p {
    font-size: 2rem;
    font-weight: 100;
}