@import url('https://fonts.googleapis.com/css?family=Pacifico&display=swap');

.Navbar {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 6vh;
}

.logo {
    margin-right: 15px;
    padding: 0 13px;
    font-size: 22px;
    background-color: #eceff1;
    font-family: 'Pacifico', cursive;
    height: 100%;
    display: flex;
    align-items: center;
}

.logo a {
    text-decoration: none;
    color: black;
}


/* SLIDER */
.slider {
    width: 340px;
    margin: 0 10px;
    display: inline-block;
}

.rc-slider-track {
    background-color: transparent;
}

.rc-slider-rail {
    height: 8px;
}

.rc-slider-handle,
.rc-slider-handle:hover,
.rc-slider-handle:active,
.rc-slider-handle:focus {
    background-color: green;
    outline: none;
    border: 2px solid green;
    box-shadow: none;
    width: 13px;
    height: 13px;
    margin-left: -7px;
    margin-top: -2px;
}

/* Select Container */

.select-container {
    margin-left: auto;
    margin-right: 2rem;
}