@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.ColorBox {
    width: 20%;
    height: 25%;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    margin-bottom: -4px;
    cursor: pointer;
}

.copy-button {
    width: 100px;
    height: 30px;
    position: absolute;
    display: inline-block;
    top: 50%;
    left: 50%;
    margin-left: -50px;
    margin-top: -15px;
    text-align: center;
    outline: none;
    background: rgba(255, 255, 255, .3);
    font-size: 1rem;
    line-height: 30px;
    text-transform: uppercase;
    border: none;
    opacity: 0;
}

.ColorBox:hover .copy-button {
    opacity: 1;
    -webkit-transition: all.5s;
    transition: all.5s;
    cursor: pointer;
}

.copy-button:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.box-content {
    position: absolute;
    width: 100%;
    left: 0px;
    bottom: 0px;
    padding: 10px;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 12px;
}

.see-more {
    background: rgba(255, 255, 255, 0.3);
    position: absolute;
    border: none;
    right: 0px;
    bottom: 0px;
    color: white;
    width: 60px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    text-transform: uppercase;
}

.copy-overlay {
    opacity: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    -webkit-transform: scale(.1);
            transform: scale(.1);
    -webkit-transition: -webkit-transform 0.6s ease-in-out;
    transition: -webkit-transform 0.6s ease-in-out;
    transition: transform 0.6s ease-in-out;
    transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}

.copy-overlay.show {
    opacity: 1;
    -webkit-transform: scale(50);
            transform: scale(50);
    z-index: 10;
    position: absolute;
}

.copy-msg {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 4rem;
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 0;
    color: white;
}

.copy-msg.show {
    -webkit-transition: all .4s ease-in-out .3s;
    transition: all .4s ease-in-out .3s;
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    z-index: 20;
}

.copy-msg h1 {
    font-weight: 400;
    text-shadow: 1px 2px black;
    background: rgba(255, 255, 255, .2);
    width: 100%;
    text-align: center;
    margin-bottom: 0;
    padding: 1rem;
}

.copy-msg p {
    font-size: 2rem;
    font-weight: 100;
}
.Palette {
    height: 100vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.Palette-colors {
    height: 90%;
}

.Palette-footer {
    background-color: #fff;
    height: 5vh;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: bold;
}

.emoji {
    font-size: 1.5rem;
    margin: 0 1rem;
}
.Navbar {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 6vh;
}

.logo {
    margin-right: 15px;
    padding: 0 13px;
    font-size: 22px;
    background-color: #eceff1;
    font-family: 'Pacifico', cursive;
    height: 100%;
    display: flex;
    align-items: center;
}

.logo a {
    text-decoration: none;
    color: black;
}


/* SLIDER */
.slider {
    width: 340px;
    margin: 0 10px;
    display: inline-block;
}

.rc-slider-track {
    background-color: transparent;
}

.rc-slider-rail {
    height: 8px;
}

.rc-slider-handle,
.rc-slider-handle:hover,
.rc-slider-handle:active,
.rc-slider-handle:focus {
    background-color: green;
    outline: none;
    border: 2px solid green;
    box-shadow: none;
    width: 13px;
    height: 13px;
    margin-left: -7px;
    margin-top: -2px;
}

/* Select Container */

.select-container {
    margin-left: auto;
    margin-right: 2rem;
}
